import './styles/index.css';
import logo2 from './assets/logo2.jpg';

const logoIcon = document.querySelector('#logoIcon');
logoIcon.src = logo2;

// Homepage Api Data
const card = document.querySelector('.card-container');

const getEpisodeList = async () => {
  const response = await fetch('https://api.tvmaze.com/search/shows?q=girls');
  const data = await response.json();
  let show = '';
  // eslint-disable-next-line array-callback-return
  data.map(dt => {
    show += `
      <div id = "${dt.show.id}"class = "cards">
       <img src="${dt.show.image.medium}" id="image" alt="" />
        <div class="heading">
           <h2>${dt.show.name}</h2>
           <span><i class="fa-solid fa-heart"></i></span>
        </div>
        <p>Rating: ${dt.show.rating.average}</p>
        <button type="submit" onclick="document.getElementById(${dt.show.id}).style.display='block'" class="comment-btn">Comments</button>
        </div>
      `;
    card.innerHTML = show;
  });
  const commentBtn = document.querySelectorAll('.comment-btn');
  commentBtn.forEach((btn) => {
    btn.addEventListener('click', (e) => {
      if (e.target.classList.contains('comment-btn')) {
        const { id } = e.target.parentNode;
        commentsPopup((id));
      }
    });
  });
};
window.addEventListener('load', getEpisodeList);

// Modal window dom activities
const modal = document.querySelector('.wrapper');

const commentsPopup = async (id) => {
  const urlData = await fetch(`https://us-central1-involvement-api.cloudfunctions.net/capstoneApi/apps/EgmzMMaC4w7c3EIH6aZ8/comments?item_id=${id}`);
  const commentsData = await urlData.json();
  console.log("COMMME", commentsData)
  const response = await fetch(`https://api.tvmaze.com/shows/${id}`);
  const data = await response.json();
  const ul = document.createElement('ul');
  let h3 = document.createElement('h3');
  if(commentsData.length){
    h3.innerHTML = `Comments (${commentsData.length})`
  } else {
    h3.innerHTML = `Comments (0)`;
  }
  ul.appendChild(h3)
  const output = `
        <div class="image-container">
          <span id = "close-btn"class="w3-button w3-display-topright">&times;</span>
        <img src="${data.image.medium}" id="epImage" alt="" />
        </div>
        <h1>${data.name}</h1>
        <p>Episode:${data.number}</p>
        <p>${data.summary}</p>
        <div class="comments">
          ${commentsData.length > 0 ? `
      ${commentsData.map((comment) => {
    const li = document.createElement('li');
    li.innerText = `${comment.creation_date} ${comment.username}: ${comment.comment}`;
    ul.appendChild(li);
  })}
      ` : ''
}

       </div>
       <h2>Add a comment</h2>
        <form action="#" class="form">
          <input type="text" placeholder="Your name" id = "name" required><br>
           <input type="hidden" value ="${data.id}"placeholder="Your name" class = "hidden" required><br>
          <textarea name="" placeholder = "Your insights" id="insight" cols="30" rows="10" required></textarea><br>
          <button type="submit" id= "submit-btn">Comment</button>
        </form>
  `;
  modal.innerHTML = output;
  document.querySelector('.comments').appendChild(ul);
  ul.classList.add('inner-container')
  modal.classList.add('active');

};

