 const postComment = async (id, username, comment) => {
  const commentsUrl = 'https://us-central1-involvement-api.cloudfunctions.net/capstoneApi/apps/EgmzMMaC4w7c3EIH6aZ8/comments'
  const response = await fetch(`${commentsUrl}`,
    {
      method: 'POST',
      body: JSON.stringify({
       item_id: id,
       username,
       comment
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    });
    return response.text;
};

// form event listener
modal.addEventListener('submit', (e) => {
  console.log("e", e)
  e.preventDefault();
  let today = new Date();
    const dd = String(today.getDate()).padStart(2, '0');
    const mm = String(today.getMonth() + 1).padStart(2, '0');
    const yyyy = today.getFullYear();
    today = `${yyyy}-${mm}-${dd}`;

  let name = document.querySelector('#name').value;
  let insight = document.querySelector('#insight').value;
  let id = document.querySelector('.hidden').value;
  console.log("ÃD>>>>>>", id)
  let commentsList = document.querySelector('.inner-container');
  console.log("commentsList", commentsList)
  commentsList.childNodes[0].innerHTML = `Comments(${commentsList.childNodes.length})`;
  const li = document.createElement('li');
  li.innerText = `${today} ${name} ${insight}`
  commentsList.appendChild(li);
  postComment(name, insight, id);
})


